// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
//import router from './router'

//第一步,安装你需要的各种ku,然后全局注册

import VueRouter from 'vue-router'
//css初始化
import './assets/css/reset.css'
//PC端的elementUI库
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

Vue.config.productionTip = false

//全局注册
Vue.use(ElementUI)
Vue.use(VueRouter)

//第二步,建立好页面之后,全部引入,直接写路由懒加载

//路由懒加载,按需加载
const homepage=resolve => {
//  require.ensure 是 Webpack 的特殊语法，用来设置 code-split point
//  代码分块
	require.ensure(['./components/homepage.vue'], () =>  {
		resolve(require('./components/homepage.vue'))
	})
}
const SampleReels=resolve => {
	require.ensure(['./components/SampleReels.vue'], () =>  {
		resolve(require('./components/SampleReels.vue'))
	})
}
const AboutUs=resolve => {
	require.ensure(['./components/AboutUs.vue'], () =>  {
		resolve(require('./components/AboutUs.vue'))
	})
}
const Test=resolve => {
	require.ensure(['./components/test.vue'], () =>  {
		resolve(require('./components/test.vue'))
	})
}
const ProfessionalSkills=resolve => {
	require.ensure(['./components/ProfessionalSkills.vue'], () =>  {
		resolve(require('./components/ProfessionalSkills.vue'))
	})
}

//第三步,路由模块,划分页面,相当于a标签点击跳转,这里就是在设置路径

const routes = [
{ path: '/', component:homepage,
children:[
//	这里是二级路由的页面内容,路径path为/就是默认展示的意思
  { path: '/', component: Test},
  { path: '/ProfessionalSkills', component:ProfessionalSkills },
  { path: '/SampleReels', component:SampleReels },
	{ path: '/AboutUs',  component:AboutUs }
]}
]

// 第四步, 创建 router 实例，然后传 `routes` 配置
// 你还可以传别的配置参数, 不过先这么简单着吧。
const router = new VueRouter({
//	  mode: 'history',//在创建的 router 对象中，如果不配置 mode，就会使用默认的 hash 模式，该模式下会将路径格式化为 #! 开头。
	  routes // （缩写）相当于 routes: routes
})


//上面router部分的重定向全部可以写在vue-router里面的index.js文件中,我是习惯全部放到入口js这里写

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



